<template>
    <div class="common-layout">
        <el-container>
            <el-header>

                <div class="header-content">
                    <el-tabs v-model="activeName" class="demo-tabs" @tab-click="handleClick">
                        <el-tab-pane name="first">
                            <template #label>
                                <span class="custom-tabs-label">
                                    <span>L3</span>
                                </span>
                            </template>
                            <a href="/" target="_self" style="text-decoration: none; color: #ffffff;font-size: 32px;
                            font-weight: 600;">&nbsp;&nbsp;&nbsp;&nbsp; 3 </a>
                        </el-tab-pane>
                        <el-tab-pane name="second">
                            <template #label>
                                <span class="custom-tabs-label">
                                    <span>M2</span>
                                </span>
                            </template>
                            <a href="/l1m2" target="_self" style="text-decoration: none; color: #ffffff;font-size: 32px;
                            font-weight: 600;"></a>
                        </el-tab-pane>
                        <el-tab-pane name="third">
                            <template #label>
                                <span class="custom-tabs-label">
                                    <span>M3</span>
                                </span>
                            </template>
                            <a href="/l2m2m3" target="_self" style="text-decoration: none; color: #ffffff;font-size: 32px;
                            font-weight: 600;"></a>
                        </el-tab-pane>
                        <el-tab-pane name="fourth">
                            <template #label>
                                <span class="custom-tabs-label">
                                    <span>M3</span>
                                </span>
                            </template>
                            <a style="text-decoration: none; color: #ffffff;font-size: 32px;
                            font-weight: 600;"></a>
                        </el-tab-pane>
                    </el-tabs>
                </div>
            </el-header>
            <el-main style="padding: 20px;">
                <div class="content-wrapper">
                    <!--  -->
                    <div class="image-container">
                        <img id="mapAll" ref="mapAll" :src="imageSrc" usemap="#image-map" class="image" />
                        <map name="image-map" id="image-map">
                            <area v-for="hotspot in hotspots" :key="hotspot.id" :shape="hotspot.shape"
                                :coords="hotspot.coords" :href="hotspot.href" @click.prevent="navigateTo(hotspot.href)"
                                @mouseover="highlightHotspot(hotspot.id)" @mouseout="unhighlightHotspot(hotspot.id)" />
                        </map>

                        <!--  -->
                        <div class="steps-button-container">
                            <el-steps :active="active" finish-status="success" align-center style="width: 100%;">
                                <el-step title="Step 1" />
                                <el-step title="Step 2" />
                                <el-step title="Step 3" />
                            </el-steps>

                            <el-button class="next-button" style="margin-top: 20px;" @click="next">
                                Next step
                            </el-button>
                        </div>
                    </div>

                    <!-- Markdown  -->
                    <div class="markdown-container">
                        <div v-html="compiledMarkdown" class="markdown-body"></div>
                    </div>
                </div>

                <!--  Step 1  -->
                <div v-if="active >= 1" class="rounded-box">
                    <div class="box-content">
                        <p>Some Text</p>
                    </div>
                </div>
            </el-main>
        </el-container>
    </div>
</template>

<script>
import $ from 'jquery';
import 'imagemapster';

export default {
    data() {
        return {
            activeName: 'fourth',
            active: 0,
            imageSrc: '/pictures/level3/L3M2M3M3.png', // 
            hotspots: [
                // ...
                {
                    id: '1',
                    shape: 'poly',
                    coords: '82,215,292,217,303,211,307,200,309,191,307,103,305,93,296,85,82,85,72,94,69,101,70,198,73,208',
                    href: '/l3m2m3m2'
                },
                {
                    id: '2',
                    shape: 'poly',
                    coords: '1414,958,1625,958,1636,953,1639,948,1640,939,1640,837,1635,830,1629,823,1624,820,1417,819,1410,820,1404,828,1397,838,1398,940,1406,951',
                    href: '/l2m2m4'
                },
            ],
            markdownText: `****

> 

****

- (Path )
- \`PlannedStmt\`\`Plan*\`



- **Path**
  - 
  - 
- **Plan**
  - 
  - 
- **(PlannedStmt)**
  - \`commandType\`\`hasReturning\`\`dependsOnRole\`
  - 

            `,
        };
    },
    computed: {
        // Markdown
        compiledMarkdown() {
            return this.$md.render(this.markdownText);
        }
    },
    methods: {
        next() {
            if (this.active++ > 2) this.active = 0;
        },
        navigateTo(url) {
            this.$router.push(url); // Vue Router
        },
        highlightHotspot(id) {
            const $map = $('#mapAll');
            $map.mapster('highlight', true, id); // 
        },
        unhighlightHotspot(id) {
            const $map = $('#mapAll');
            $map.mapster('set', false, id); // 
        },
        goBack() {
            this.$router.go(-1)
        }
    },
    mounted() {
        $('#mapAll').mapster({
            fillColor: '1AC4F9',
            strokeColor: "FFFFFF",
            strokeWidth: 3,
            fillOpacity: 0.6,
            singleSelect: true,
            highlight: false,
        });
    }
};
</script>

<style scoped>
.common-layout {
    height: 100%;
    /*  */
}

.image-container {
    position: relative;
    width: 100%;
    height: 100%;
    /*  */
    overflow: hidden;
    /*  */
}

img {
    width: 100%;
    /*  */
    height: 100%;
    /*  */
    object-fit: contain;
    /*  */
}

.rounded-box {
    position: absolute;
    top: 20%;
    /*  */
    left: 30%;
    /*  */
    width: 200px;
    /*  */
    height: 100px;
    /*  */
    background-color: rgba(255, 255, 255, 0.5);
    /*  */
    border: 2px solid black;
    /*  */
    border-radius: 10px;
    display: flex;
    align-items: center;
    justify-content: center;
}

.box-content p {
    margin: 0;
    font-size: 16px;
    color: black;
}

.header-content {
    display: flex;
    align-items: center;
    /*  */
    justify-content: flex-start;
    gap: 10px;
    /*  */
}

.header-content .back-button {
    line-height: 40px;
    /*  */
    padding: 10px 25px;
    /*  */
    font-size: 20px;
    /*  */
    /*  */
}


.header-content .title {
    font-size: 40px;
    /*  */
    font-weight: bold;
    /*  */
    line-height: 40px;
    /*  */
}

.next-button {
    font-size: 22px;
    /*  */
    padding: 15px 30px;
    /*  */
    height: 60px;
    /*  */
    width: 200px;
    /*  */
    line-height: 60px;
    /*  */
    border-radius: 5px;
    /*  */
}
</style>